.price {

	.container();
	
	position: relative;
	padding-top: 20px;
	padding-bottom: 50px;
	background-color: #353535;
	
	&::before {
		
		content: url(../img/angle-price.svg);
		position: absolute;
		top: -34px;
		left: 0;
		display: block;
		width: 320px;
		height: 34px;
	}
	
	&__header {
		
		margin-top: 0;
		text-align: center;
		text-transform: uppercase;
		color: #FFF;
	}

	&__table {
		
		width: 100%;
		padding: 0 20px;
		
		tbody {
			
			display: block;
		}

		td {
			
			display: block;
			color: #FFF;
		}

		tr {
			
			display: flex;
			-webkit-flex-wrap: wrap;
			    -ms-flex-wrap: wrap;
			        flex-wrap: wrap;
			-webkit-justify-content: space-between;
			        justify-content: space-between;
			padding-bottom: 15px;
			font-size: 20px;
			line-height: 27px;
		}

		.col-2 {
			
			font-size: 20px;
		}

		.col-3 {

			width: 100%;
			-webkit-order: 4;
			    -ms-order: 4;
			        order: 4;
		}

		.col-1 {
			display: none;
		}

	}

	&__discount {
		
		position: relative;
		display: block;
		margin: 0 auto;
		width: 280px;
		padding: 20px 20px 20px 120px;
		font-size: 20px;
		line-height: 27px;
		color: #FFF;
		border: 1px solid #3c3c3c;
		
		&::before {
			
			content: url(../img/clock-image.svg);
			position: absolute;
			top: 15px;
			left: 25px;
			display: block;
			width: 60px;
			height: 60px;
			-webkit-box-sizing: content-box;
			   -moz-box-sizing: content-box;
			        box-sizing: content-box;
		}
	}
}

@media (min-width:@tablet) {
	
	.price {

		position: relative;
		width: 768px;
		
		&::before {
			
			content: url(../img/angle-price-tab.svg);
			position: absolute;
			top: -40px;
			left: 0;
			display: block;
			width: 768px;
			height: 40px;
		}
		
		&__wrapper {
			
			.container();
			
		}

		&__header {
			
			text-align: left;
		}

		&__table {
			
			border-collapse: collapse;
		}
		
		tbody {
		
			display: table-row-group;
		}

		tr {
			
			display: table-row;
			font-weight: normal;
		}

		td {
			
			display: table-cell;
			padding: 10px;
			border: 1px solid #3c3c3c;
		}

		.col-1 {
			
			display: table-cell;
			padding-left: 15px;
			padding-right: 15px;
			text-align: center;
			color: #555;
		}

		.col-3 {
			
			width: auto;
		}

		.col-2 {
			
			font-weight: bold;
		}

		&__discount {
			
			width: 100%;
			padding: 40px;
			padding-left: 200px;
			font-size: 30px;
			line-height: 27px;
			text-transform: uppercase;
			
			&::before {
				
				top: 22px;
				left: 40px;
			}
		}
	}
}

@media (min-width:@desktop) {
	
	.price {
		
		.clearfix();
		
		width: 1200px;
		margin: 0 auto;
		padding-top: 50px;
		
		&::before {
			
			content: none;
		}

		&__table {
			
			float: left;
			width: 630px;
			margin: 0;
			
			td {
				
				padding: 12px;
			}

			.col-1 {
				
				padding-left: 18px;
				padding-right: 18px;
			}
		}

		&__discount {
			
			float: right;
			width: 265px;
			font-size: 20px;
			line-height: 27px;
			padding: 190px 50px 15px;
			text-align: center;
			text-transform: lowercase;
			
			&::before {
				
				left: 62px;
				width: 140px;
				height: 140px;
			}
		}
	}
}