.news {
	
	&__wrapper {

		.container();
		position: relative;
		min-height: 480px;
		padding-top: 40px;
		padding-bottom: 28px;	
		background-color: #FFF;
		border-bottom: 1px solid #e5ddd5;
		
		&::before {
			content: url(../img/angle-news.svg);
			position: absolute;
			top: -34px;
			left: 0;
			display: block;
			width: 320px;
			height: 34px;
		}
	}

	&__title {
		
		margin: 0;
		margin-bottom: 28px;
		font-size: 30px;
		line-height: 30px;
		text-align: center;
		text-transform: uppercase;
		
	}

	&__show-all {
		position: absolute;
		bottom: 29px;
		left: 20px;
		display: block;
		width: 278px;
		padding: 20px;
		background-color: #FFF;
		font-size: 24px;
		font-weight: bold;
		line-height: 24px;
		letter-spacing: -1pt;
		text-transform: uppercase;
		border: 1px solid #E5DDD5;
		cursor: pointer;
		  	
		  	&:hover {

		  		border-color: #D5C9BD;
		  	}

		  	&:active {
		  		
		  		background-color: #D5C9BD;
		  	}
	}

	&__list {
		
		margin: 0;
		padding: 0;
		list-style: none;
	}

	&__item {

		position: relative;
		min-height: 127px;
		margin: 0 20px;
		margin-bottom: 16px;
		padding-bottom: 18px;
		padding-left: 70px;
		
		
		&:first-child {
			border-bottom: 1px solid #E5DDD5;
		}
		
	}

	&__item:nth-child(2) {

		height: 155px;
		margin-bottom: 0;
	}

	&__item:nth-child(n+3) {
		display: none;
	}

	&__item-date {
		position: absolute;
		top: 10px;
		left: 0;
		width: 55px;
		height: 70px;
		padding-top: 5px;
		background: url(../img/news-date.svg) no-repeat;
		color: #FFF;
		text-align: center;
		
		&:hover {
			
			background: url(../img/news-date--hover.svg) no-repeat;
			color: #FFF;
			text-decoration: none;
		}

		&:active {

			background: url(../img/news-date--hover.svg) no-repeat;
			color: #978374;
			text-decoration: none;
		}		
		
		& strong {
			font-size: 30px;
			line-height: 30px;
		}

		& span {
			display: block;
			font-size: 11px;
			line-height: 11px;
			text-transform: uppercase;
		}
		
	}
}

@media (min-width:@tablet) {
	
	.news {
	
		position: relative;
		background-color: #FFF;
		width: 768px;
		margin: 0 auto;
		border-bottom: 1px solid #E5DDD5;
		
		&::before {
			content: url(../img/angle-tablet.svg);
			position: absolute;
			top: -40px;
			left: 0;
			display: block;
			width: 768px;
			height: 40px;
		}

	  &__wrapper {

		.clearfix();
	  	min-height: 245px;
	  	border-bottom: none;
		
		&::before {
			display: none;
		}
	  	
		
	  }

	  &__title {
		
		display: inline-block;
		height: 50px;
		margin-bottom: 50px;
		font-size: 40px;
		font-stretch: ultra-condensed;
		line-height: 30px;
		letter-spacing: -1px;
		text-align: left;
	  }

	  &__show-all {
	  	
	  	position: static;
	  	display: inline-block;
	  	float: right;
	  	width: 160px;
	  	margin-top: -10px;
	  	padding: 10px 1px	;
	  	font-size: 18px;
	  	line-height: 24px;
		  	
		  	&:hover {

		  		border-color: #D5C9BD;
		  	}

		  	&:active {
		  		
		  		background-color: #D5C9BD;
		  	}
	  	
	  }

	  &__list {
		
			font-size: 0;
	  }

	  &__item {
			display: inline-block;
			width: 318px;
			min-height: 110px;
			margin: 0;
			padding-right: 10px;
			padding-left: 70px;
			font-size: 18px;
			line-height: 27px;

			vertical-align: top;
			
			&:first-child {
				
				border-bottom: none;
			}
			
			&-delet {
				
				display: none;
			}
	  }

	  &__item:nth-child(2) {
	  	height: 130px;
	  }
  }
}

@media (min-width:@desktop) {
	
	.news {
	
		width: 1200px;
    padding-top: 20px;
    padding-bottom: 38px;
     
    &:before {
     	
     	top: -62px;
     	width: 1200px;
     	height: 66px;
    	}

	  &__title {
		
		margin-left: 50px;
	  }

	  &__item:nth-child(3) {
		
		display: inline-block;
	  }

	  &__item:nth-child(n+4) {
		
		display: none;
	  }
    }
}