.stats {


  .container();

  &__header {
    min-height: 130px;
    border: 1px solid @gray-darken;
    
    padding: 27px 38px 26px;
    color: @black;
    background-color: #FFF;
  }

  &__slogan {
  	display: block;
  	margin-bottom: 16px;
  	
  	font-weight: bold;
  	font-size: 28px;
  	line-height: 30px;
  	text-align: center;
  	text-transform: uppercase;
  }

  &__intro {
  	margin: 0;
  	
  	font-size: 16px;
  	line-height: 22px;
  	text-align: center;
  }

  &__legend {
  	display: block;
  	
  	font-size: 20px;
  	line-height: 20px;
  	
  	&--bottom {
  		padding-top: 20px;
  		padding-bottom: 14px;
  		
  		text-align: center;
  		color: rgba(255, 255, 255, .3);
  		
  		background-color: #242424;
  	}

  	&--top {
  		display: none;
  	}

  	& sup {
  		top: -0.05em;
  		
  		font-size: 90%;
  	}
  }

  &__list {
  	width: 100%;
  	margin-top: 0;
  	margin-bottom: 0;
  	border-collapse: collapse;
    
    & tr {
    	color: #FFF;
    	
    	background-color: #404040;
    	
    	&:nth-child(2) {
    		background-color: #4e4e4e;
    	}

    	&:nth-child(3) {
    		background-color: #353535;
    	}

    	&:nth-child(4) {
    		background-color: #404040;
    	}
    }
  }

  .stats__value,
  .stats__field {
  	width: 50%;
  	padding: 20px;
  	padding-bottom: 19px;
  	
  	vertical-align: top;
  }

  .stats__value {
  	padding-right: 0;
  	
  	font-size: 40px;
  	line-height: 40px;
  	
  	&-sup {
  		padding-top: 7px;
  	}
  	
  	& sup {
  		top: 0.35em;
  		
  		margin-left: 5px;
  	}
  }

  .stats__field {
  	padding-left: 0;
  	
  	font-size: 18px;
  	line-height: 18px;
  	text-align: right;
  }
}

@media (min-width:@tablet) {
  
  .stats {

    width: 768px;
    background-color: #353535;
    
    &__wrapper {
       
      .container();
      
      position: relative;
      z-index: 1;
    
      margin-top: -183px;      
    }

    &__header {
     
      .clearfix();
      
      min-height: 129px;
      padding: 0;
      padding-top: 31px;
      padding-bottom: 24px;
      
      &::before {
      	content: "";
      	position: absolute;
      	top: 27px;
      	left: -14px;
      	
      	border: 15px solid transparent;
      	border-bottom-color: #FFF;
      	transform: rotate(45deg);
        }
      }

      &__slogan {
      	float: left;
      	width: 280px;
      	padding-left: 40px;
      	margin-bottom: 20px;
      	
      	font-size: 40px;
      	line-height: 40px;
      	text-align: left;
      	text-transform: none;
      }

      &__intro {
      	float: right;
      	width: 260px;
      	padding: 0;
      	padding-top: 4px;
      	padding-right: 60px;
      	
        font-size: 18px;
      	line-height: 24px;
      	text-align: left;
      }

      &__legend--top {
      	display: block;
      	float: left;
      	clear: left;
      	width: 280px;
      	padding-left: 40px;
      	
      	color: rgba(0, 0, 0, .3);
      }

      &__legend--bottom {
      	display: none;
      }

    &__list {
            
      .clearfix();
      
      display: block;
      
      & tr {
      	display: block;
      	float: left;
      	width: 50%;
      	padding: 25px 30px;
      	padding-bottom: 28px;
      }
    }

    .stats__value,
    .stats__field {
    	display: block;
    	width: auto;
    	padding: 0;
    }

    .stats__value {

    	margin-bottom: 7px;
    	font-size: 72px;
    	line-height: 72px;

    	&-sup {
    		margin-top: -17px;
    	}
    }

    .stats__field {
    	font-size: 20px;
    	line-height: 27px;
    	text-align: left;
    }

    .stats__field br {
    	display: none;
    }
  }
}

@media (min-width:@desktop) {

  .stats {

    width: 1200px;

    &__wrapper {

  	.clearfix();  	

  	margin-top: -160px;
    
    }
  
    &__header {

      min-height: 319px;
      
      float: left;
      width: 320px;
      
      &::before {
        top: 22px;
      }
    }

    &__slogan {
      margin-bottom: 40px;
    }

    &__intro {
      width: 280px;
      padding-right: 20px;
    }

    &__legend--top {
    	margin-top: 25px;
    }

    &__items {
      float: left;
      width: 640px;
      min-height: 320px;
      
      border: 1px solid @gray-darken;
    }
  }
}