
.page-footer {
	color: #FFF;
	
	&__wrapper {
		
		.container();
		position: relative;
		background-color: #292929;
		border-top: 1px solid #3e3e3e;
		border-bottom: 1px solid #3e3e3e;
		
		&::before {
			content: "";
			position: absolute;
			top: -35px;
			left: 0;
			display: block;
			width: 100%;
			height: 34px;
			background-image: url(../img/angle-footer.svg);
			
		}
		
	}

	&__address {

		width: 280px;
		margin: 30px auto;
		font-size: 18px;
		text-align: center;
		
		& p {
			
			margin-bottom: 20px;
			padding: 0 20px;
		}
		
		& a {

			display: block;
			padding: 12px 75px;
			border: 1px solid #3e3e3e;
			color: #FFF;
		}

		& a:hover {
			
			border-color: #515151;
			text-decoration: none;
		}

		& a:active {
			
			padding: 13px 76px;
			background-color: #252525;
			border: none;
		}
	}

	&__social {
		
		margin: 0 auto 35px;
		padding: 0;
		list-style: none;
	}

	&__social-item {
		
		position: relative;
		display: inline-block;
		margin: 0;
		padding: 0;
		vertical-align: top;
		
		& a {

			display: block;
			margin-left: 1px;
			padding: 39px 50px;
			border: 1px solid #3e3e3e;
			font-size: 0;
		}

		& a:hover {
			
			border-color: #515151;
			text-decoration: none;
		}

		& a:active {
			
			background-color: #252525;
			border: none;
		}
	}

	&__social-item:nth-child(2) {

		border-left: none;
		border-right: none;
	}
	
	&__designed {
		
		width: 280px;
		margin-top: 0;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 30px;
		
		& p {
			
			display: block;
			margin-bottom: 20px;
			font-size: 18px;
			line-height: 27px;
			text-align: center;
		}

		& a {
			
			display: block;
			padding: 12px 75px;
			border: 1px solid #3e3e3e;
			font-size: 18px;
			line-height: 24px;
			text-align: center;
			color: #FFF;
			text-transform: uppercase;
			text-decoration: none;
		}

		& a:hover {
			
			border-color: #515151;
			text-decoration: none;
		}

		& a:active {
			
			padding: 13px 76px;
			background-color: #252525;
			border: none;
		}
	}

}

.social__icon::before {
	content: "";
	position: absolute;
	top: 30px;
	left: 33px;
	display: block;
	width: 40px;
	height: 40px;
	background-color: #0FF;
}

.social__vk::before {
	background: url(../img/vk-icon.svg) no-repeat 55% 50%;
}

.social__f::before {
	background: url(../img/facebook-icon.svg) no-repeat 55% 50%;
}

.social__inst::before {
	background: url(../img/insta-icon.svg) no-repeat 55% 50%;
}


@media (min-width:@tablet) {
	.page-footer{

		width: 768px;
		margin: 0 auto;
		background-color: #292929;


		&__wrapper {

			.container();
			.clearfix();

			position: relative;
			background-color: #292929;
			
			&::before {
				display: none;
			}
		}

		&__address {
		
			display: inline-block;
			width: 410px;
			margin-bottom: 190px;	
			font-stretch: ultra-condensed;
			text-align: left;
			
			& a {
				
				border: none;
				padding: 0;
				text-decoration: none;
				cursor: default;
				
				&:hover,
				&:active {
				
					padding: 0;
					text-decoration: none;
					background-color: #292929;
					border: none;
				}
			}

			& p {
				
				padding: 0;
				margin-bottom: 5px;
			}
		}

		&__designed {
			
			float: right;
			display: inline-block;
			width: 160px;
			margin-top: 19px;
			
			& p {
				
				margin-bottom: 8px;
			}
			
			& a {
				
				padding: 12px 20px;
				
				&:active {
					
					padding: 13px 21px;
				}
			}
		}

		&__social {
			
			position: absolute;
			bottom: 0px;
			display: block;
		}
			
		&__social-item {
				
			width: 210px;
			
			& a {
				
				text-align: center;
			}
			
		}
	}
	
	.social__inst::before {
		left: 87px;
	}
	
	.social__vk::before {
		left: 90px;
	}

	.social__f::before {
		left: 90px;
	}
}


@media (min-width:@desktop) {
	.page-footer{

		width: 1200px;
		
		&__address {
			
			float: left;
			margin-bottom: 20px;
		}

		&__social{
		
			position: static;
			float: left;
			display: inline-block;
			padding-top: 62px;
			
			
			&-item {

				width: 48px;
				margin-left: 6px;
				
				& a {

					padding: 12px 24px;
					letter-spacing: 0;
					
					&:before {
						
						width: 23px;
						height: 23px;
						top: 12px;
						left: 15px;
						-webkit-background-size: cover;
						     -o-background-size: cover;
						        background-size: cover;
						
					}
				}
			}

		}
		
	}
}
