.preview {
	

	&__wrapper {
		
		.container();
		
		padding-top: 30px;
		background-color: #FFF;
		
	}

	&__title {
		
		margin-top: 0;
		margin-bottom: 25px;
		font-size: 30px;
		line-height: 30px;
  		letter-spacing: -1px;
		text-align: center;
		text-transform: uppercase;
	}

	&__write {
		
		display: none;
	}

	&__list {
		
		position: relative;
		width: 280px;
		margin-bottom: 0;
		padding-bottom: 70px;
		list-style: none;
	}

	&__item {
		
		position: relative;
		padding-left: 70px;
  		letter-spacing: -1px;
		
		& h2 {
			
			margin-top: 0;
		}

		& p {
			
			font-size: 18px;
			line-height: 27px;
		}
	}

	&__img-wrap {

			position: absolute;
			top: 15px;
			left: 0;
			display: block;
			width: 49px;
			height: 49px;
			border: 1px solid #eae3dc;
			overflow: hidden;
			
		& img {
			
			max-width: 100%;
			height: 100%;
		}
	}

	&__slider {

		position: absolute;
		bottom: 0;
		padding-left: 100px;
		padding-right: 80px;
		padding-bottom: 50px;

	}

	&__slider-items {
		
		position: relative;
		padding-left: 22px;
	}

	&__slider-icon {
		
		content: "";
		position: absolute;
		top: 5px;
		left: 7px;
		display: block;
		width: 8px;
		height: 8px;
		border: 2px solid #858483;
		-webkit-transform: rotate(45deg);
		   -moz-transform: rotate(45deg);
		    -ms-transform: rotate(45deg);
		     -o-transform: rotate(45deg);
		        transform: rotate(45deg);
		cursor: pointer;
	}

	&__input {

		display: none;
	}

	&__input:checked + &__slider-icon {

		background-color: #353535;
		
	}

	&__btn1:checked [data="name1"] {
		
		display: block;
		position: relative;
		padding-left: 70px;
  	letter-spacing: -1px;
	}
}

@media (min-width:@tablet) {
	
	.preview {
		
		width: 768px;
		margin: 0 auto;
		background-color: #FFF;
		
		&__wrapper {
		
			padding-top: 50px;
			padding-bottom: 50px;
		}
		
		&__title {
			
			display: inline-block;
			text-align: left;
		}


		&__write {
		
			display: inline-block;
			  	float: right;
		  	width: 160px;
		  	margin-top: -10px;
		  	padding: 10px 1px	;
		  	font-size: 18px;
		  	font-weight: bold;
		  	text-transform: uppercase;
		  	line-height: 24px;
		  	letter-spacing: -1px;
		  	background-color: #FFF;
		  	outline: none;
		  	border: 1px solid #e5ddd5;
		  	cursor: pointer;
		  	
		  	&:hover {

		  		border-color: #D5C9BD;
		  	}

		  	&:active {
		  		
		  		background-color: #D5C9BD;
		  	}
		}

		&__list-wrapper {
			
			position: relative;
		}

		&__list {

			position: relative;
			width: 100%;
			height: 160px;
			margin-top: 20px;
			padding-left: 0;
			padding-bottom: 0;
			border-top: 1px solid #e5ddd5;
			border-bottom: 1px solid #e5ddd5;
		}

		&__left-arrow,
		&__right-arrow {
			
			position: absolute;
			z-index: 1;
			display: block;
			padding: 80px 19px;
			border-left: 1px solid #e5ddd5;
			border-right: 1px solid #e5ddd5;
			background-image: url(../img/slider-arrow.svg);
			background-repeat: no-repeat;
			-webkit-background-size: 12px 12px;
			     -o-background-size: 12px 12px;
			        background-size: 12px 12px;
			background-position: center;
			cursor: pointer; 
		}

		&__left-arrow {
			
			top: 0;
			left: 0;
			
			&:hover {
				border-color: #D5C9BD;
		  	}

		  	&:active {
		  		
		  		background-color: #D5C9BD;
		  	}
		}

		&__right-arrow {
			
			top: 0;
			right: 0;
			transform: rotate(180deg);
			
			&:hover {
				border-color: #D5C9BD;
		  	}

		  	&:active {
		  		
		  		background-color: #D5C9BD;
		  	}
		}

		&__item {
			
			position: relative;
			padding: 20px 60px 20px 190px;
			
			& h2 {
				
				font-size: 24px;
				line-height: 27px;
			}
		}

		&__img-wrap {
			
			position: absolute;
			top: 40px;
			left: 75px;
			display: block;
			width: 80px;
			height: 80px;
			-webkit-border-radius: 50%;
			        border-radius: 50%;
		}

		&__slider {
			
			position: absolute;
			bottom: -15px;
			left: 280px;
			display: block;
			padding: 5px 20px;
			background-color: #FFF;
		}
	}
}


@media (min-width:@desktop) {
	.preview {

		width: 1200px;
		margin: 0 auto;
		background-color: #FFF;
		
		&__slider {
			
			left: 430px;
		}
	}
}