.photo {
	
	&__wrapper {
		
		.container();
		
		padding-top: 30px;
		padding-bottom: 40px;
		background-color: #F8F5F2;
		
		& h1 {
			
			margin: 0;
			font-size: 30px;
			font-weight: bold;
			line-height: 30px;
			text-align: center;
			text-transform: uppercase;
		}

	}

	&__slider {
		
		padding-top: 40px;
	}

	&__client {
		
		position: relative;
		width: 280px;
		margin: 0 auto;
		padding: 0;
		
		&>p {
			
			content: "";
			position: absolute;
			top: 390px;
			left: 0;
			display: block;
			width: 175px;
			height: 50px;
			padding-top: 12px;
			padding-left: 25px;
			font-size: 20px;
			line-height: 27px;
			text-transform: uppercase;
			background: url(../img/label.png) no-repeat;
			color: #FFF;

		}

		& h4 {
			
			padding-left: 20px;
			font-size: 24px;
			font-weight: bold;
			line-height: 27px;
			text-align: left;
		}
	}

	&__text {
		
		padding-left: 20px;
		padding-right: 20px;
		font-size: 18px;
		line-height: 27px;
		
	}

	&__services {
		
		.clearfix();
		padding-bottom: 20px;
		margin: 0 20px 20px 20px;
		border-bottom: 1px solid #f2ede8;
	}

	&__service {
		
		position: relative;
		display: inline-block;
		float: left;
		width: 120px;
		margin: 0;
		padding-left: 30px;
		font-size: 16px;
		line-height: 27px;
		text-align: center;
		
		&::before {
			
			content: "";
			position: absolute;
			top: 3px;
			left: 15px;
			display: block;
			width: 20px;
			height: 20px;
		}
	}

	&__scissors::before {
		
		content: url(../img/scissors.svg);
	}

	&__shaving::before {
		
		content: url(../img/beard1.svg);
	}

	&__mustache::before {
		
		content: url(../img/mustache.svg);
		left: 5px;
		width: 45px;
	}

	&__slide-2 &__shaving,
	&__slide-2 &__mustache,
	&__slide-3 &__scissors,
	&__slide-4 &__shaving,
	&__slide-4 &__mustache {
			
			display: none;
	}
}

.home-page {
	
	display: none;
}

@media (min-width:@tablet) {
	
	.photo {
		
		position: relative;
		width: 768px;
		margin: 0 auto;
		margin-top: -200px;
		padding-bottom: 40px;
		background-color: #F8F5F2;
		
		&::before {

		content: url(../img/angle-form-tab.svg);
		position: absolute;
		top: -37px;
		left: 0;
		display: block;
		width: 768px;
		height: 34px;
		}

		&__wrapper {
			
			.container();
			.clearfix();
			
			padding-top: 50px;

			h1 {
				
				display: inline-block;
				font-size: 40px;
				text-align: left;
			}
		}

		&__client-1 {
			
			.clearfix();
			position: relative;
			margin: 0;
			margin-bottom: 40px;

			&>p {
				
				top: 20px;
			}

			img {
				
				float: left;
				width: 280px;
				height: 480px;
			}

			h4 {
				
				padding-left: 40px;
				padding-bottom: 20px;
			}
		}
			
		&__caption-main {
				
			position: absolute;
			top: 0px;
			left: 280px;
			display: block;
			width: 330px;
			height: 480px;
			padding-top: 20px;
			border: 1px solid #f2ede8;
			border-left: none;
		}

		&__text {
			
			padding-left: 40px;
			padding-right: 25px;
			
		}

		&__services {
			
			position: absolute;
			top: 390px;
			left: 0;
			padding-left: 10px;
			border-bottom: none;
		}

		&__service {
			
			padding-bottom: 5px;
		}

		&__client-2,
		&__client-3,
		&__client-4  {
			
			.clearfix();
			position: relative;
			margin: 0;
			margin-bottom: 40px;
			
			h4 {
				
				padding-left: 40px;
			}
			
			img {
				
				float: left;
				width: 270px;
				height: 294px;
			}
			
			.photo__caption {
				
				position: absolute;
				top: 0px;
				left: 270px;
				display: block;
				width: 340px;
				height: 294px;
				padding-top: 20px;
				border: 1px solid #f2ede8;
				border-left: none;
				
				.photo__text {
					
					padding-left: 65px;
				}
				
				.photo__services {
					
					top: 240px;
				}
			}
		}
	}
}

@media (min-width:@desktop) {
	.photo {
		
		position: relative;
		width: 1200px;
		margin: 0 auto;
		margin-top: -200px;
		
		&::before {
			
			content: url(../img/angle-form-desk.svg);
			position: absolute;
			top: -70px;
			left: 0;
			display: block;
			width: 1200px;
			height: 70px;
		}

		&__client-1 {
			
			&>p {
				
				left: 780px;
			}
			
			img {
				
				width: 480px;
			}
		}

		&__caption-main {
			
			width: 480px;
		}

		&__slide-block {
			
			.clearfix();
			
		}

		&__slide {
			
			float: left;
			margin-right: 60px;
			
			&:last-child {
				
				margin-right: 0;
			}
		}	

		&__client-2,
		&__client-3, 
		&__client-4 {
			
			position: relative;
			
			img {
				
				float: none;
				width: 294px;
				height: 294px;
			}
			
			.photo__caption {
				
				position: static;
				width: 280px;
				height: 200px;
				margin-top: -4px;
				padding-top: 0;
				border: 1px solid #f2ede8;
				border-top: none;

				.photo__text {
				
					padding-left: 40px;
				}

				.photo__services {
					
					top: 450px;
				}

				.photo__service {
					
					width: 110px;
				}
			}
		}
	}
}