.advantage {
	
	
	&__wrapper {
		
		.container();
		
		background-color: #242424;
		min-height: 320px;
		padding-top: 27px;
	}

	&__list {

		margin: 0;
		padding: 0;
		padding-bottom: 25px;
		list-style: none;
				
	}
	

	&__item {

		position: relative;
		height: 145px;
		margin-bottom: 23px;
		padding: 123px 30px 20px;
		color: #FFF;
		-webkit-box-sizing: content-box;
		   -moz-box-sizing: content-box;
		        box-sizing: content-box;
	}

	&__item-fast {
		
		&::before {
			content: url(../img/fast.svg);
			position: absolute;
			top: 10px;
			left: 110px;
			display: block;
			width: 95px;
			height: 94px;
		}
	}

	&__item-cool {
		
		&::before {
			content: url(../img/cool.svg);
			position: absolute;
			top: 10px;
			left: 110px;
			display: block;
			width: 95px;
			height: 94px;
		}
	}

	&__item-expensive {
		
		&::before {
			content: url(../img/expensive.svg);
			position: absolute;
			top: 10px;
			left: 110px;
			display: block;
			width: 95px;
			height: 94px;
		}
	}

	&__item:nth-child(n+2) {
	  display: none;
	}

	&__slogan {
		display: block;
		text-align: center;
		font-size: 30px;
		line-height: 30px;
	}

	&__text {
		font-size: 20px;
		line-height: 27px;
		text-align: center;
	}

	&__slider {
		padding-left: 122px;
		padding-right: 122px;
		padding-bottom: 30px;
		
	}

	&__slider-items {
		
		position: relative;
		padding-left: 22px;
	}

	&__slider-icon {
		
		content: "";
		position: absolute;
		top: 5px;
		left: 7px;
		display: block;
		width: 8px;
		height: 8px;
		border: 2px solid #959595;
		-webkit-transform: rotate(45deg);
		   -moz-transform: rotate(45deg);
		    -ms-transform: rotate(45deg);
		     -o-transform: rotate(45deg);
		        transform: rotate(45deg);
		cursor: pointer;
	}

	&__input {

		display: none;
	}

	&__input:checked + &__slider-icon {

		background-color: #fff;
		
	}


}

@media (min-width:@tablet) {
  .advantage {

  	width: 768px;
  	margin: 0 auto;
  	background-color: #353535;
  	
	&__slider {
		display: none;
	}

  	&__wrapper {

  		padding-top: 42px;
  		padding-bottom: 60px;
  		background-color: #353535;
  	}

  	&__list {
  		padding-bottom: 5px;
  	}

  	&__item {
  		height: 95px;
  		margin: 0 30px 32px;
  		padding: 0;
  		padding-left: 128px;
  		padding-bottom: 35px;
  		border-bottom: 1px solid #4a4a4a;
  		
  		&-fast:before,
  		&-cool:before,
  		&-expensive:before {
  			left: 10px;
  		}
  	}

  	&__item:nth-child(n+2) {
  		display: block;
  	}

    &__item:nth-child(3) {
    	margin-bottom: 0;
    	border-bottom: none;
    }

		&__slogan {
			
			padding-left: 20px;
			text-align: left;
			font-size: 30px;
			line-height: 30px;
		}

		&__text {

			padding-left: 20px;
			font-size: 20px;
			line-height: 27px;
			letter-spacing: -1px;
			text-align: left;
		}

  }

}


@media (min-width:@desktop) {
	
	.advantage {

		width: 1200px;
		
		&__wrapper {
			
			.container();
			
			padding: 0;
		}

		&__only-mobile {
			display: none;
		}
		
		&__list {

			display: table;
			width: 100%;
			height: 280px;
		}

		&__item {

			display: table-cell;
			width: 320px;
			padding: 180px 30px 0;
			border-bottom: none;
  		
  			&-fast:before,
  			&-cool:before,
  			&-expensive:before {

  				top: 50px;
  				left: 130px;
  		}
		}

		&__item:nth-child(n+2) {

			display: table-cell;
			height: 280px;
		}

		&__slogan {
			
			text-align: center;
		}

		&__text {
			
			text-align: center;
		}
	}

}