//     : @import './src/blocks/page-header/page-header.less';

@import '../../less/variables.less';     //      



.page-header__logo {
  background-color: #242424;
  text-align: center;
  
  & img {
    width: 230px;
    height: 82px;
  }
}

.main-nav {
  position: relative;
  
  .container();
  background-color: #826550;

  &__toggle {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    
    display: block;
    width: 70px;
    height: 50px;
    
    font-size: 0;
    
    background-color: #6A4D38;
    cursor: pointer;
    
    border: none;
    
    &:hover {
      background-color: #624530;
    }

    &:active {
      background-color: #624530;
    }
  }

  &--closed &__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    
    width: 36px;
    height: 2px;
    
    background-color: #FFF;
    -webkit-box-shadow: 0 8px 0 0#FFF,
                        0 16px 0 0#FFF;
            box-shadow: 0 8px 0 0#FFF,
                        0 16px 0 0#FFF;
  }

  &--closed &__toggle:active::before {
    background-color: rgba(255, 255, 255, .3);
    -webkit-box-shadow: 0 8px 0 0 rgba(255, 255, 255, .3),
                        0 16px 0 0 rgba(255, 255, 255, .3);
            box-shadow: 0 8px 0 0 rgba(255, 255, 255, .3),
                        0 16px 0 0 rgba(255, 255, 255, .3);
  }

  &--opened &__toggle {
    top: 0;
    right: auto;
    left: 0;
    
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 24px;
      left: 18px;
      
      width: 36px;
      height: 2px;
      
      background-color: #FFF;
    }

    &::before {
      transform: rotate(45deg);
      -webkit-box-shadow: none;
              box-shadow: none;
    }

    &::after {
      transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
      background-color: rgba(255, 255, 255, .3);
    }
  }

    &__list,
    &__item,
    &__user-list {
      display: block;
    }

    &__list,
    &__user-list {
      margin: 0;
      padding: 0;
      
      list-style: none;
    }

    &__item,
    &__user-list {
      min-height: 50px;
    }

    &__list {
      font-weight: normal;
    }

    &__item a {
      display: block;
      padding: 12px 21px;
      padding-bottom: 13px;
      
      font-family: 'PT Sans Narrow', sans-serif;
      font-size: 18px;
      font-stretch: ultra-condensed;
      letter-spacing: 0.01px;
      line-height: 24px;
      color: #FFF;
      text-transform: uppercase;
      text-decoration: none;
      
      background-color: #826550;
      border-top: 1px solid #886e5b;
      border-bottom: 1px solid #71543f;
    }

    &__item a[href] {
      &:hover {
        background-color: #755A46;
        border-top-color: #755a46;
      }

      &:active {
        color: rgba(255, 255, 255, .3);
        
        background-color: #6A4D38;
        border-top-color: #6a4d38;
      }
    }

    &__user-login {
      position: relative;
      
      display: block;
      padding: 17px 21px;
      padding-left: 49px;
      
      line-height: 18px;
      color: #FFF;
      text-decoration: none;
      
      &::before {
        content: "";
        position: absolute;
        top: 17px;
        left: 21px;
        
        width: 16px;
        height: 16px;
        
        background-image: url("../img/login.png");
        background-position: 0 -80px;
        opacity: 0.3;
      }
    }

}

@media @mobile-widths-only {
  .main-nav--closed .main-nav__item {
    display: none;
    
    &--active {
      display: block;
      
      & a {
        padding-right: 70px;
        padding-left: 70px;
        
        text-align: center;
      }
    }
  }
  .main-nav--closed .main-nav__user-list {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;
    
    height: 80vh;
    
    background-color: #6A4D38;
    
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      
      width: 7px;
      height: 100%;
      
      -webkit-box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, .4);
              box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, .4);
    }
  }

  .main-nav--nojs {
    & .main-nav__toggle {
    display: none;
    }

    & .main-nav__wrapper {
      position: static;
      
      height: auto;
    }

    & .main-nav__item {
      display: block;
    }

    & .main-nav__item--active a {
      padding-right: 21px;
      padding-left: 21px;
      
      text-align: left;
    }

    & .main-nav__user-list {
      display: block;
      
      background-color: #6A4D38;
    }
  }
}

@media (min-width:@tablet) {
  .page-header {
  	position: relative;
  	
  	min-height: 70px;
  	padding-bottom: 397px;
    background-color: #242424;
  }

  .page-header__logo {
  	position: absolute;
  	bottom: 0;
  	width: 100%;
  	height: 397px;
    background-color: #242424;
    background-image: url("../img/bg-header.jpg");
    background-repeat: no-repeat;
    background-position: center -9px;
    text-align: center;
  	
    & img {
  	 display: inline-block;
      width: 370px;
      height: 101px;
      margin-top: 48px;
    }
  }

.main-nav {

  width: auto;
  margin: 0;
  
  background: @black;
  
  &__wrapper {
    .container();
    .clearfix()
  }

  &__toggle {
    display: none;
  }


    &__list {
      float: left;
      width: 570px;
    }

    &__item {
      float: left;
      margin-right: 21px;
      
      &:last-child {
        margin-right: 0;
      }

      &--active {
        position: relative;
        
        &::after {
          content: "";
          position: absolute;
          right: 0;
          bottom: -3px;
          left: 0;
          
          height: 3px;
          
          background: #826550;
        }
      }

      & a {
        padding: 0;
        padding-top: 25px;
        padding-bottom: 23px;
        
        background: none;
        border: none;
      }

      & a[href] {
        &:hover {
          color: #826550;
          background-color: @black;
        }

        &:active {
          color: #FFF;
          background-color: @black;
          opacity: 0.3;
        }
      }
    }

    &__user-list {
      float: right;
      
    }

    &__user-login {
      width: 60px;
      height: 70px;
      padding: 0;
      font-size: 0;
      
      &::before {
        top: 29px;
        left: 12px;
      }

      &:hover::before {
        background-position: -16px -80px;
        opacity: 1;
      }

      &:active::before {
        background-position: -16px -80px;
        opacity: 0.3;
      }
    }


  }
}

@media (min-width:@desktop) {
  .page-header {
  	min-width: 1200px;
  	padding-bottom: 648px;
  }

  .page-header__logo {
  	height: 648px;
    
    background-position: center 0px;
    -webkit-background-size: cover;
         -o-background-size: cover;
            background-size: cover;
    
    & img {
      display: inline-block;
      width: 370px;
      height: 153px;
      margin-top: 140px;
    }
  }


.main-nav {

  width: auto;

    &__list {
      width: 780px;
    }

    &__item {
      margin-right: 0;
      
      &--activ::after {
        right: 31px;
        left: 31px;
      }
    }

    &__item a {
      padding-right: 20px;
      padding-left: 20px;
    }

    &__item a[href] {
      &:hover {
        color: @black;
        
        background-color: #FFF;
      }

      &:active {
        color: rgba(0, 0, 0, .3);
        background-color: #FFF;
        opacity: 1;
      }
    }

    &__user-list {
      width: 160px;
      margin-right: 20px;
    }

    &__user-login {
      position: relative;
      
      width: auto;
      height: auto;
      padding: 25px 30px 23px 50px;
     
      font-size: 18px;
      line-height: 24px;
      text-transform: uppercase;
      text-decoration: none;
      
      &:hover {
        color: #000;
        background-color: #FFF;
      }

      &:active {
        color: rgba(0, 0, 0, .3);
        background-color: #FFF;
      }

      &:active::before {
        opacity: 0.1;
      }
    }
  }
}

  
