.record {

	
	&__wrapper {
		
		.container();
		
		padding-top: 30px;
		padding-bottom: 70px;
		background-color: #f8f5f2;	
		
		& h1 {
			margin-top: 0;
			margin-bottom: 30px;
			font-size: 30px;
			font-weight: bold;
			line-height: 30px;
			list-style: -1px;
			text-align: center;
			text-transform: uppercase;
		}
	}
}

.home-page {
	
	display: none;
}

.form {
	
	padding: 0 20px;
	color: #000;
	
	&__name,
	&__contacts {
		
		margin-bottom: 30px;
	}

	
	&__textarea,
	&__text {
		
		width: 100%;
		margin-bottom: 5px;
		padding: 12px 17px;
		font-size: 18px;
		line-height: 30px;
		letter-spacing: -1px;
		border: 1px solid #E5DDD5;
		outline: none;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
	}

	&__text:hover,
	&__textarea:hover {
		
		border-color: #d5c9bd;
		outline: none;
	}

	&__text:active,
	&__textarea:active {
		
		border-color: #999;
		outline: none;
	}

	&__text:focus,
	&__textarea:focus {
		
		border-color: #999;
		outline: none;
	}

	&__textarea {
		
		height: 109px;
		margin-bottom: 40px;
	}

	&__beard {
		
		border: none;
		margin: 0;
		margin-bottom: 40px;
		padding: 0;
		
		& legend {
			text-align: center;
			text-transform: uppercase;
		}
	}

	&__model {
		
		position: relative;
		display: inline-block;
		width: 76px;
		margin: 70px 7px 20px;
		padding-left: 15px;
		padding-right: 15px;
		padding-bottom: 100px;
		font-size: 18px;
		line-height: 24px;
		text-transform: lowercase;
		
		&::before {
			
			content: "";
			position: absolute;
			top: 35px;
			left: 7px;
			display: block;
			width: 67px;
			height: 105px;
			background-repeat: no-repeat;
		}

		&-1::before {
			
			left: 8px;
			background-image: url(../img/beard1.svg);
		}

		&-2::before {
			
			background-image: url(../img/beard2.svg);
		}

		&-3::before {

			left: 12px;			
			background-image: url(../img/beard3.svg);
		}

		&-4::before {
			
			background-image: url(../img/beard4.svg);
		}

		&-5::before {

			left: 17px;			
			background-image: url(../img/beard5.svg);
		}

		&-item {
			
			position: absolute;
			left: 17px;
			top: -60px;
			display: block;
			width: 48px;
			height: 48px;
			border: 1px solid #e5ddd5;
			-webkit-border-radius: 50%;
			        border-radius: 50%;
			background-color: #FFF;
			
			&:hover {
				
				border-color: #d5c9bd;
			}

			&:active {
				
				border: 15px solid #D5C9BD;
			}
		}
	}

	&__radio {
		
		display: none;
	}

	&__radio:checked + &__model-item::before {
		
		content: "";
		position: absolute;
		top: 15px;
		left: 15px;
		display: block;
		width: 16px;
		height: 16px;
		border: 1px solid #E5DDD5;
		-webkit-border-radius: 50%;
		        border-radius: 50%;
		background-color: #f8f5f2;
	}

	&__services {
		
		border: none;
		margin: 0;
		margin-bottom: 40px;
		padding: 0;
		
		& legend {
			
			margin-bottom: 40px;
			text-align: center;
		}
	}

	&__service {
		
		position: relative;
		display: block;
		margin-bottom: 20px;
		padding-left: 60px;
		font-size: 18px;
		line-height: 30px;
		
		&-item {
			
			position: absolute;
			top: -5px;
			left: 0;
			display: block;
			width: 34px;
			height: 34px;
			border: 1px solid #E5DDD5;
			background-color: #FFF;
			
			&:hover {
				
				border-color: #D5C9BD;
			}

			&:active {
				
				background-color: #D5C9BD;
			}
		}
	}
	
	&__check {
		
		display: none;
	}

	&__check:checked + &__service-item::before {
		
		content: "";
		position: absolute;
		top: 7px;
		left: 7px;
		display: block;
		width: 20px;
		height: 10px;
		border: 2px solid #000;
		border-top: none;
		border-right: none;
		transform: rotate(-45deg);
	}

	&__submit {
		
		width: 100%;
		padding: 12px;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		text-transform: uppercase;
		background-color: #826550;
		color: #FFF;
		cursor: pointer;
		
		&:hover {
			
			background-color: #6a4d38;
		}

		&:active {
			
			background-color: #6a4d38;
			color: #978374;
		}

		&:disabled {
			
			background-color: #AEAEAE;
			cursor: auto;
		}
	}

}


@media (min-width:@tablet) {
	
	.record {

		position: relative;
		width: 768px;
		margin: 0 auto;
		background-color: #F6F4F0;
	
		&::before {
			
			content: url(../img/angle-form-tab.svg);
			position: absolute;
			top: -37px;
			left: 0;
			display: block;
			width: 768px;
			height: 40px;
		}

		&__wrapper {
		
			.container();

			padding-top: 60px;
			padding-bottom: 70px;
			background-color: #f8f5f2;
		
		
			& h1 {

				float: left;
				margin-top: 0;
				margin-bottom: 30px;
				font-size: 40px;
				font-weight: bold;
				line-height: 30px;
				text-align: left;
				text-transform: uppercase;
			}
		}
	}

	.home-page {
		
		display: block;
		float: right;
		margin-top: -5px;
		padding: 8px 30px;
		font-size: 18px;
		font-weight: bold;
		line-height: 24px;
		border: 1px solid #e5ddd5;
		text-transform: uppercase;
		color: #000;
		
		&:hover {
			
			text-decoration: none;
			border-color: #D5C9BD;
			color: #000;
		}

		&:active {
			
			text-decoration: none;
			background-color: #D5C9BD;
			color: #958C84;
		}
		
	}

	.form {
		
		padding: 0;
		padding-top: 60px;
		
		&__name {
			
		.clearfix();
		}

		&__text-name {
			
			float: left;
			margin: 0;
			width: 33.3%;
			border: 1px solid #E5DDD5;
			
			&:nth-child(2) {
				border-right: none;
				border-left: none;
			}
		}

		&__contacts {

			margin-bottom: 0;
		}
			
		&__text-contacts {
			
			margin: 0;
			margin-bottom: 30px;
			border: 1px solid #E5DDD5;
			
		}

		&__textarea {
			
			height: 210px;
		}

		&__beard {
			
			border-top: 1px solid #D5C9BD;
			
			& h3 {
				
				padding-left: 25px;
				padding-right: 25px;
				background-color: #f8f5f2;
			}
		}

		&__model {
			
			width: 115px;
			margin-left: 0;
			margin-right: 12px;
			font-size: 24px;
			line-height: 30px;
			text-align: center;
			
			&:last-child {
				
				margin-right: 0;
			}

			&-1::before {
				
				left: 25px;
			}

			&-2::before {
				
				left: 23px;
			}

			&-3::before {
				
				left: 22px;
			}

			&-4::before {
				
				left: 26px;
			}

			&-5::before {
				
				left: 33px;
			}

			&-item {
				
				left: 33px;
			}
		}

		&__services {
			
			border-top: 1px solid #D5C9BD;
			
			& h3 {
				
				padding-left: 25px;
				padding-right: 25px;
				background-color: #f8f5f2;
			}
		}

		&__service {
			
			float: left;
			width: 50%;
			font-size: 24px;
			line-height: 30px;
		}
	}
}


@media (min-width:@desktop) {
	
	.record {

		position: relative;
		width: 1200px;
		margin: 0 auto;
		background-color: #F6F4F0;
	
		&::before {
			
			content: url(../img/angle-form-desk.svg);
			position: absolute;
			top: -70px;
			left: 0;
			display: block;
			width: 1200px;
			height: 70px;
		}
	}

	.form {
		
		&__text-name {
			
			width: 293px;
			margin-right: 40px;
			
			&:nth-child(2) {
				
				border: 1px solid #E5DDD5;
				
				&:hover {
					
					border-color: #d5c9bd;
					outline: none;
				}

				&:active,
				&:focus {
					
					border-color: #999;
					outline: none;
				}
			}
			
			&:last-child {

				margin-right: 0;
			}
		}

		&__text-contacts {
			
			float: left;
			width: 460px;
			margin-right: 40px;
			
			&:last-child {
				
				margin-right: 0;
			}
		}

		&__model {
			
			margin-right: 62px;
			margin-left: 20px;
			
			&:last-child {
				
				margin-right: 20px;
			}
		}

		&__service {
			
			width: 320px;
		}
	}
}